import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    notes: [{ datetime: "2019-06-02", id: 1, sort: 2, content: "" }],
    curnotes: {},
    cardcolor: "",
    count: 1,
    ExitActive: true,
    EditsortActive: true,
    cursort: { name: "預設筆記本", id: 2 },
    sorts: [{ name: "所有筆記本", id: 1 }, { name: "預設筆記本", id: 2 }, { name: "test", id: 3 }]
  },
  mutations: {
    AddNote(state) {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1 >= 10 ? date.getMonth() + 1 : "0" + (date.getMonth() + 1);
      let day = date.getDate() >= 10 ? date.getDate() : "0" + date.getDate();
      let curdate = year + "-" + month + "-" + day;
      let note = {
        datetime: curdate,
        content: "",
        sort: state.cursort.id == 1 ? 2 : state.cursort.id,
        id: ++state.count,
      };
      state.notes.push(note);
      state.curnotes = note;
    },
    DeleteNote(state, id) {
      let deleteindex = 0;
      state.notes.forEach((e, index) => {
        if (e.id == id)
          deleteindex = index;
      });
      state.notes.splice(deleteindex, 1);
    },
    UpdateNote() { },
    EditNote(state, id) {

      state.notes.forEach(e => {
        if (e.id === id) {
          e.content = state.curnotes.content;
        }
      });
    },
    ChangeSort(state, text) {
      state.cursort = text;
    },
    AddSort(state, text) {
      let count = state.sorts[state.sorts.length - 1].id + 1;
      state.sorts.push({ name: text, id: count });
    },
    DeleteSort(state, text) {
      state.sorts.push(text);
    },
    ChangeSortname(state, sort) {
      state.sorts.forEach(e => {
        if (e.id == sort.id) {
          e.name = sort.name;
        }
      })
    },

  },
  actions: {
    addnote({ commit }) {
      commit("AddNote");
    },
    editnote({ commit }, id) {
      commit("EditNote", id);
    },
    deletenote({ commit }, id) {
      commit("DeleteNote", id);
    },
    changesort({ commit }, text) {
      commit("ChangeSort", text);
    },
    addsort({ commit }, text) {
      commit("AddSort", text);
    }
  },
  getters: {
    notescard: state => state.notes.filter(e => {
      if (state.cursort.id == 1) {
        return true;
      }
      else {
        if (e.sort == state.cursort.id)
          return true;
      }

    }),
    curnotes: state => state.curnotes,
    exitactive: state => state.ExitActive,
    editsortactive: state => state.EditsortActive,
    sorts: state => state.sorts,
    cursort: state => state.cursort,
    sortnum: state => {
      let obj = { 1: 0 };
      let self = state;
      state.notes.forEach(e => {

        self.sorts.forEach(ex => {
          if (e.sort == ex.id) {
            if (obj[ex.id]) {
              obj[ex.id]++;
            }
            else {
              obj[ex.id] = 1;
            }

          } else {
            if (!obj[ex.id]) {
              obj[ex.id] = 0;
            }
          }

        });
        obj[1]++;
      });
      console.log(obj);
      return obj;
    },
  },
  modules: {
  }
})
