<template>
  <div class="wrap dateblack" @click="editcard">
    <h1 class="date gray">{{ date }}</h1>
    <div class="content">
      {{ content }}
    </div>
  </div>
</template>
<script>
export default {
  name: "notescard",
  props: ["date", "content", "sort", "id", "item"],
  data() {
    return {};
  },
  methods: {
    editcard() {
      this.$store.state.ExitActive = false;
      this.$store.state.curnotes = this.item;
    },
  },
};
</script>
<style scoped>
.wrap {
  width: calc(50% - 60px);
  text-align: center;
  margin: 10px 30px;
  height: 400px;
  box-sizing: border-box;
  overflow: hidden;
}

.content {
  width: 100%;
  word-break: break-all;
  font-size: 25px;
}
</style>